import React, { useState, useRef, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Checkbox } from '@/components/ui/checkbox';
import { Edit2, Check, X, Trash2 } from 'lucide-react';
import { cn } from '@/lib/utils';
import { dataService } from '@/services/DataService';
import { TodoEntry } from '@/types/DataModels';
import { updateTodoData } from '@/utils/todoHelpers';

interface InlineEditableTaskProps {
  task: TodoEntry;
  onTaskUpdated?: (task: TodoEntry) => void;
  onTaskDeleted?: (taskId: string) => void;
  className?: string;
  compact?: boolean;
}

export const InlineEditableTask: React.FC<InlineEditableTaskProps> = ({
  task,
  onTaskUpdated,
  onTaskDeleted,
  className,
}) => {
  const [isEditing, setIsEditing] = useState(false);
  const [title, setTitle] = useState(task.title || '');
  const [isSubmitting, setIsSubmitting] = useState(false);
  const inputRef = useRef<HTMLInputElement>(null);

  useEffect(() => {
    setTitle(task.title || '');
  }, [task.title]);

  useEffect(() => {
    if (isEditing && inputRef.current) {
      inputRef.current.focus();
      inputRef.current.select();
    }
  }, [isEditing]);

  const handleSave = async () => {
    if (!title?.trim() || isSubmitting) return;

    setIsSubmitting(true);
    try {
      const todoUpdateData = updateTodoData(task, {
        title: title.trim()
      });

      const result = await dataService.todos.update(task.id, todoUpdateData);

      if (result.success && result.data) {
        onTaskUpdated?.(result.data);
        setIsEditing(false);
      }
    } catch (error) {
      console.error('Failed to update task:', error);
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleCancel = () => {
    setTitle(task.title || '');
    setIsEditing(false);
  };

  const handleToggleComplete = async () => {
    try {
      const result = task.isCompleted 
        ? await dataService.todos.markIncomplete(task.id)
        : await dataService.todos.markComplete(task.id);
      
      if (result.success && result.data) {
        onTaskUpdated?.(result.data);
      }
    } catch (error) {
      console.error('Failed to toggle task completion:', error);
    }
  };

  const handleDelete = async () => {
    if (confirm('Are you sure you want to delete this task?')) {
      try {
        const result = await dataService.todos.delete(task.id);
        if (result.success) {
          onTaskDeleted?.(task.id);
        }
      } catch (error) {
        console.error('Failed to delete task:', error);
      }
    }
  };

  const handleKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSave();
    } else if (e.key === 'Escape') {
      handleCancel();
    }
  };

  if (isEditing) {
    return (
      <div className={cn(
        "flex items-center gap-3 p-2 rounded-lg border bg-card",
        className
      )}>
        <Checkbox
          checked={task.isCompleted}
          onCheckedChange={handleToggleComplete}
          className="flex-shrink-0"
        />
        
        <Input
          ref={inputRef}
          value={title}
          onChange={(e) => setTitle(e.target.value)}
          onKeyDown={handleKeyDown}
          className="flex-1"
          disabled={isSubmitting}
        />
        
        <div className="flex gap-1">
          <Button
            size="sm"
            onClick={handleSave}
            disabled={!title?.trim() || isSubmitting}
            className="h-8 px-2"
          >
            <Check className="h-3 w-3" />
          </Button>
          
          <Button
            variant="ghost"
            size="sm"
            onClick={handleCancel}
            className="h-8 px-2"
          >
            <X className="h-3 w-3" />
          </Button>
        </div>
      </div>
    );
  }

  return (
    <div className={cn(
      "flex items-center gap-3 p-2 rounded-lg border transition-colors hover:bg-muted/50 group",
      task.isCompleted && "opacity-60",
      className
    )}>
      <Checkbox
        checked={task.isCompleted}
        onCheckedChange={handleToggleComplete}
        className="flex-shrink-0"
      />
      
      <div 
        className={cn(
          "flex-1 text-sm cursor-pointer",
          task.isCompleted && "line-through text-muted-foreground"
        )}
        onClick={() => setIsEditing(true)}
      >
        {task.title || 'Untitled Task'}
      </div>
      
      <div className="flex gap-1 opacity-0 group-hover:opacity-100 transition-opacity">
        <Button
          variant="ghost"
          size="sm"
          onClick={() => setIsEditing(true)}
          className="h-8 w-8 p-0"
        >
          <Edit2 className="h-3 w-3" />
        </Button>
        
        <Button
          variant="ghost"
          size="sm"
          onClick={handleDelete}
          className="h-8 w-8 p-0 text-red-600 hover:text-red-700"
        >
          <Trash2 className="h-3 w-3" />
        </Button>
      </div>
    </div>
  );
}; 