import { TodoEntry } from "@/types/DataModels";

/**
 * Todo Helper Functions
 * Ensures consistent field arrangement across all todo operations
 * Uses application field names (lowercase) internally, converts to spreadsheet format when needed
 */

// Standard field order based on TodoEntry interface (lowercase for application use)
export interface TodoCreateData {
  // BaseEntity fields (auto-generated)
  // id: string;
  // createdAt: string;
  // updatedAt: string;
  // isDeleted?: boolean;

  // TodoEntry specific fields (in application format - lowercase)
  personId?: string;
  title: string;
  description?: string;
  dueDate?: string;
  priority: "low" | "medium" | "high";
  category: string;
  status: "active" | "completed";
  isCompleted: boolean;
  completedAt?: string;
  reminder?: boolean;
  reminderTime?: string;
  subtasks?: Array<{
    id: string;
    title: string;
    isCompleted: boolean;
    completedAt?: string;
  }>;
  notes?: string;
  tags?: string[];
}

export interface TodoUpdateData {
  personId?: string;
  title?: string;
  description?: string;
  dueDate?: string;
  priority?: "low" | "medium" | "high";
  category?: string;
  status?: "active" | "completed";
  isCompleted?: boolean;
  completedAt?: string;
  reminder?: boolean;
  reminderTime?: string;
  subtasks?: Array<{
    id: string;
    title: string;
    isCompleted: boolean;
    completedAt?: string;
  }>;
  notes?: string;
  tags?: string[];
}

/**
 * Creates a new todo with consistent field arrangement (application format)
 */
export function createTodoData(params: {
  title: string;
  priority?: "low" | "medium" | "high";
  category?: string;
  description?: string;
  dueDate?: string;
  notes?: string;
  tags?: string[];
  personId?: string;
}): TodoCreateData {
  return {
    // Keep fields in application format (lowercase)
    personId: params.personId,
    title: params.title.trim(),
    description: params.description || "",
    dueDate: params.dueDate,
    priority: params.priority || "medium",
    category: params.category || "General",
    status: "active",
    isCompleted: false,
    completedAt: undefined,
    reminder: false,
    reminderTime: undefined,
    subtasks: undefined,
    notes: params.notes || "",
    tags: params.tags || [],
  };
}

/**
 * Updates a todo with consistent field arrangement (application format)
 */
export function updateTodoData(
  existingTodo: TodoEntry,
  updates: Partial<{
    title: string;
    description: string;
    dueDate: string;
    priority: "low" | "medium" | "high";
    category: string;
    status: "active" | "completed";
    isCompleted: boolean;
    completedAt: string;
    reminder: boolean;
    reminderTime: string;
    notes: string;
    tags: string[];
  }>,
): TodoUpdateData {
  return {
    // Keep fields in application format (lowercase)
    personId: existingTodo.personId,
    title: updates.title?.trim() || existingTodo.title,
    description:
      updates.description !== undefined
        ? updates.description
        : existingTodo.description,
    dueDate:
      updates.dueDate !== undefined ? updates.dueDate : existingTodo.dueDate,
    priority: updates.priority || existingTodo.priority,
    category: updates.category || existingTodo.category,
    status: updates.status || existingTodo.status,
    isCompleted:
      updates.isCompleted !== undefined
        ? updates.isCompleted
        : existingTodo.isCompleted,
    completedAt:
      updates.completedAt !== undefined
        ? updates.completedAt
        : existingTodo.completedAt,
    reminder:
      updates.reminder !== undefined ? updates.reminder : existingTodo.reminder,
    reminderTime:
      updates.reminderTime !== undefined
        ? updates.reminderTime
        : existingTodo.reminderTime,
    subtasks: existingTodo.subtasks,
    notes: updates.notes !== undefined ? updates.notes : existingTodo.notes,
    tags: updates.tags || existingTodo.tags,
  };
}

/**
 * Mark todo as completed with consistent field arrangement (application format)
 */
export function markTodoComplete(): TodoUpdateData {
  return {
    status: "completed",
    isCompleted: true,
    completedAt: new Date().toISOString(),
  };
}

/**
 * Mark todo as incomplete with consistent field arrangement (application format)
 */
export function markTodoIncomplete(): TodoUpdateData {
  return {
    status: "active",
    isCompleted: false,
    completedAt: undefined,
  };
}

/**
 * Converts application format (lowercase) to spreadsheet format (capitalized)
 * Used when sending data to spreadsheet API
 */
export function convertToSpreadsheetFormat(
  data: TodoCreateData | TodoUpdateData,
): Record<string, any> {
  const result: Record<string, any> = {};

  // Convert each field from lowercase to capitalized
  if (data.personId !== undefined) result.PersonID = data.personId;
  if (data.title !== undefined) result.Title = data.title;
  if (data.description !== undefined) result.Description = data.description;
  if (data.dueDate !== undefined) result.DueDate = data.dueDate;
  if (data.priority !== undefined) result.Priority = data.priority;
  if (data.category !== undefined) result.Category = data.category;
  if (data.status !== undefined) result.Status = data.status;
  if (data.isCompleted !== undefined) result.IsCompleted = data.isCompleted;
  if (data.completedAt !== undefined) result.CompletedAt = data.completedAt;
  if (data.reminder !== undefined) result.Reminder = data.reminder;
  if (data.reminderTime !== undefined) result.ReminderTime = data.reminderTime;
  if (data.subtasks !== undefined) result.Subtasks = data.subtasks;
  if (data.notes !== undefined) result.Notes = data.notes;
  if (data.tags !== undefined) result.Tags = data.tags;

  return result;
}

/**
 * Converts spreadsheet raw data to proper TodoEntry format
 * Handles boolean conversion and field mapping
 */
export function convertFromSpreadsheetFormat(
  rawData: Record<string, any>,
): Partial<TodoEntry> {
  // Helper function to convert spreadsheet boolean strings to actual booleans
  const parseBoolean = (value: any): boolean => {
    if (typeof value === "boolean") return value;
    if (typeof value === "string") {
      const normalized = value.toLowerCase().trim();
      return normalized === "true" || normalized === "1";
    }
    return Boolean(value);
  };

  // Helper function to parse JSON strings safely
  const parseJSON = (value: any): any => {
    if (typeof value === "string" && value.trim()) {
      try {
        return JSON.parse(value);
      } catch {
        return value;
      }
    }
    return value;
  };

  return {
    // Handle both capitalized and lowercase field names from spreadsheet
    id: rawData.ID || rawData.id,
    personId: rawData.PersonID || rawData.personId,
    title: rawData.Title || rawData.title,
    description: rawData.Description || rawData.description,
    dueDate: rawData.DueDate || rawData.dueDate,
    priority: rawData.Priority || rawData.priority || "medium",
    category: rawData.Category || rawData.category || "General",
    status: rawData.Status || rawData.status || "active",
    isCompleted: parseBoolean(rawData.IsCompleted || rawData.isCompleted),
    completedAt: rawData.CompletedAt || rawData.completedAt,
    reminder: parseBoolean(rawData.Reminder || rawData.reminder),
    reminderTime: rawData.ReminderTime || rawData.reminderTime,
    subtasks: parseJSON(rawData.Subtasks || rawData.subtasks),
    notes: rawData.Notes || rawData.notes,
    tags: parseJSON(rawData.Tags || rawData.tags) || [],
    createdAt: rawData.CreatedAt || rawData.createdAt,
    updatedAt: rawData.UpdatedAt || rawData.updatedAt,
  };
}

/**
 * Formats todo data for display with consistent field access
 */
export function formatTodoForDisplay(todo: TodoEntry) {
  return {
    id: todo.id,
    title: todo.title,
    description: todo.description || "",
    dueDate: todo.dueDate,
    priority: todo.priority,
    category: todo.category,
    status: todo.status,
    isCompleted: todo.isCompleted,
    completedAt: todo.completedAt,
    createdAt: todo.createdAt,
    updatedAt: todo.updatedAt,
    notes: todo.notes || "",
    tags: todo.tags || [],
    reminder: todo.reminder || false,
    reminderTime: todo.reminderTime,
  };
}
