import React, { useState, useRef, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Plus, Check, X } from 'lucide-react';
import { cn } from '@/lib/utils';
import { dataService } from '@/services/DataService';
import { TodoEntry } from '@/types/DataModels';
import { createTodoData } from '@/utils/todoHelpers';

interface QuickAddTaskProps {
  onTaskAdded?: (task: TodoEntry) => void;
  className?: string;
  compact?: boolean;
}

export const QuickAddTask: React.FC<QuickAddTaskProps> = ({ 
  onTaskAdded, 
  className,
  compact = false 
}) => {
  const [isOpen, setIsOpen] = useState(false);
  const [title, setTitle] = useState('');
  const [isSubmitting, setIsSubmitting] = useState(false);
  const inputRef = useRef<HTMLInputElement>(null);

  useEffect(() => {
    if (isOpen && inputRef.current) {
      inputRef.current.focus();
    }
  }, [isOpen]);

  const handleSubmit = async () => {
    if (!title.trim() || isSubmitting) return;

    setIsSubmitting(true);
    try {
      const todoData = createTodoData({
        title: title,
        priority: 'medium',
        category: 'General'
      });

      const result = await dataService.todos.create(todoData);

      if (result.success && result.data) {
        onTaskAdded?.(result.data);
        setTitle('');
        setIsOpen(false);
      }
    } catch (error) {
      console.error('Failed to create task:', error);
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleCancel = () => {
    setTitle('');
    setIsOpen(false);
  };

  const handleKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter') {
      e.preventDefault();
      handleSubmit();
    } else if (e.key === 'Escape') {
      handleCancel();
    }
  };

  if (!isOpen) {
    return (
      <Button
        variant={compact ? "ghost" : "outline"}
        size={compact ? "sm" : "default"}
        onClick={() => setIsOpen(true)}
        className={cn(
          "gap-2",
          compact ? "h-8 px-3 text-xs" : "w-full justify-start h-10",
          className
        )}
      >
        <Plus className={cn("h-4 w-4", compact && "h-3 w-3")} />
        {compact ? "Add" : "Add new task"}
      </Button>
    );
  }

  return (
    <div className={cn("flex gap-2 p-2 border border-dashed border-primary/30 rounded-lg bg-primary/5", className)}>
      <Input
        ref={inputRef}
        value={title}
        onChange={(e) => setTitle(e.target.value)}
        placeholder="Type your task here..."
        onKeyDown={handleKeyDown}
        className="flex-1"
        disabled={isSubmitting}
      />
      
      <div className="flex gap-1">
        <Button
          size="sm"
          onClick={handleSubmit}
          disabled={!title.trim() || isSubmitting}
          className="px-3"
        >
          <Check className="h-3 w-3 mr-1" />
          {isSubmitting ? 'Saving...' : 'Save'}
        </Button>
        
        <Button
          variant="ghost"
          size="sm"
          onClick={handleCancel}
          className="px-2"
        >
          <X className="h-3 w-3" />
        </Button>
      </div>
    </div>
  );
}; 