import React, { useState, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { CheckCircle2, Search, Eye, EyeOff } from 'lucide-react';
import { dataService } from '@/services/DataService';
import { TodoEntry } from '@/types/DataModels';
import { DashboardLayout } from '@/components/DashboardLayout';
import { QuickAddTask } from '@/components/ui/QuickAddTask';
import { InlineEditableTask } from '@/components/ui/InlineEditableTask';

const TodosPage: React.FC = () => {
  const [todos, setTodos] = useState<TodoEntry[]>([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [showCompleted, setShowCompleted] = useState(false);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    loadTodos();
  }, []);

  const loadTodos = async () => {
    try {
      setLoading(true);
      const result = await dataService.todos.getAll();
      if (result.success && result.data) {
        setTodos(result.data.data);
      } else {
        setError(result.error || 'Failed to load todos');
      }
    } catch {
      setError('Failed to load todos');
    } finally {
      setLoading(false);
    }
  };

  const handleTaskAdded = (newTask: TodoEntry) => {
    setTodos(prev => [newTask, ...prev]);
  };

  const handleTaskUpdated = (updatedTask: TodoEntry) => {
    setTodos(prev => prev.map(task => 
      task.id === updatedTask.id ? updatedTask : task
    ));
  };

  const handleTaskDeleted = (taskId: string) => {
    setTodos(prev => prev.filter(task => task.id !== taskId));
  };

  // Filter todos
  const filteredTodos = todos.filter(todo => {
    // Search filter
    if (searchTerm && !todo.title.toLowerCase().includes(searchTerm.toLowerCase())) {
      return false;
    }
    
    // Show/hide completed
    if (!showCompleted && todo.isCompleted) {
      return false;
    }
    
    return true;
  });

  const activeTodos = todos.filter(todo => !todo.isCompleted);
  const completedTodos = todos.filter(todo => todo.isCompleted);

  if (loading) {
    return (
      <DashboardLayout>
        <div className="flex items-center justify-center h-64">
          <div className="text-center">
            <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary mx-auto"></div>
            <p className="mt-2 text-muted-foreground">Loading tasks...</p>
          </div>
        </div>
      </DashboardLayout>
    );
  }

  return (
    <DashboardLayout>
      <div className="space-y-6">
        {/* Header */}
        <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4">
          <div>
            <h1 className="text-2xl font-bold">Tasks</h1>
            <p className="text-muted-foreground">Simple todo list</p>
          </div>
          
          <div className="flex items-center gap-2">
            {completedTodos.length > 0 && (
              <Button
                variant="outline"
                size="sm"
                onClick={() => setShowCompleted(!showCompleted)}
                className="gap-2"
              >
                {showCompleted ? <EyeOff className="h-4 w-4" /> : <Eye className="h-4 w-4" />}
                {showCompleted ? 'Hide' : 'Show'} completed ({completedTodos.length})
              </Button>
            )}
          </div>
        </div>

        {/* Quick Add Task */}
        <QuickAddTask 
          onTaskAdded={handleTaskAdded}
          className="max-w-2xl"
        />

        {/* Search */}
        <Card>
          <CardContent className="p-4">
            <div className="relative">
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-muted-foreground" />
              <Input
                placeholder="Search tasks..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="pl-10"
              />
            </div>
          </CardContent>
        </Card>

        {/* Error Display */}
        {error && (
          <Card className="border-red-200 bg-red-50">
            <CardContent className="p-4">
              <div className="flex items-center gap-2 text-red-800">
                <span>{error}</span>
                <Button variant="ghost" size="sm" onClick={() => setError(null)}>
                  
                </Button>
              </div>
            </CardContent>
          </Card>
        )}

        {/* Tasks List */}
        <div className="space-y-4">
          {filteredTodos.length === 0 ? (
            <Card>
              <CardContent className="p-8 text-center">
                <CheckCircle2 className="h-12 w-12 mx-auto text-muted-foreground mb-4" />
                <h3 className="text-lg font-medium mb-2">No tasks found</h3>
                <p className="text-muted-foreground mb-4">
                  {searchTerm 
                    ? 'Try adjusting your search term'
                    : activeTodos.length === 0 
                    ? 'Add your first task above to get started'
                    : 'All your tasks are completed!'
                  }
                </p>
              </CardContent>
            </Card>
          ) : (
            <div className="grid gap-3">
              {filteredTodos.map(todo => (
                <InlineEditableTask
                  key={todo.id}
                  task={todo}
                  onTaskUpdated={handleTaskUpdated}
                  onTaskDeleted={handleTaskDeleted}
                />
              ))}
            </div>
          )}
        </div>

        {/* Stats */}
        <Card>
          <CardHeader>
            <CardTitle className="text-sm">Summary</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="flex gap-4 text-sm">
              <span>{activeTodos.length} active</span>
              <span>{completedTodos.length} completed</span>
              <span>{todos.length} total</span>
            </div>
          </CardContent>
        </Card>
      </div>
    </DashboardLayout>
  );
};

export default TodosPage; 