// Unified Data Service - Centralized data operations
// This service provides a clean interface that can be easily migrated from spreadsheet to database

import {
  createSpreadsheetRow,
  readSpreadsheetData,
  updateSpreadsheetRow,
  deleteSpreadsheetRow,
  KnownSheetName,
} from "@/lib/spreadsheetAPI";
import {
  WorkoutLogEntry,
  Exercise,
  WorkoutTemplate,
  WorkoutSession,
  VitalSigns,
  BodyMeasurement,
  NutritionEntry,
  MeditationEntry,
  HealthEntry,
  SleepEntry,
  TodoEntry,
  TodoCategory,
  ApiResult,
  FilterOptions,
  PaginationOptions,
  DataResponse,
  EntityKey,
  EntityType,
} from "@/types/DataModels";
import {
  markTodoComplete,
  markTodoIncomplete,
  convertToSpreadsheetFormat,
  convertFromSpreadsheetFormat,
} from "@/utils/todoHelpers";

// Sheet mapping for different entity types
const SHEET_MAPPING: Record<EntityKey, KnownSheetName> = {
  workoutLogs: "Workout_Logs",
  exercises: "Exercises",
  workoutTemplates: "WorkoutTemplates",
  workoutSessions: "Workout_Sessions",
  vitalSigns: "Vital_Signs",
  bodyMeasurements: "Body_Measurements",
  nutritionEntries: "Nutrition_Log",
  meditationEntries: "Meditation_Log",
  healthEntries: "Health_Tracking",
  sleepEntries: "Sleep_Log",
  todoEntries: "ToDo",
  todoCategories: "ToDo",
};

// Generic data transformation functions
function generateId(): string {
  return `${Date.now()}-${Math.random().toString(36).substr(2, 9)}`;
}

function addTimestamps<T extends Record<string, unknown>>(
  data: T,
): T & { createdAt: string; updatedAt: string; id: string } {
  const now = new Date().toISOString();
  return {
    ...data,
    id: (data.id as string) || generateId(),
    createdAt: (data.createdAt as string) || now,
    updatedAt: now,
  };
}

// Base Data Service Class
class BaseDataService<T extends Record<string, unknown>> {
  constructor(private entityKey: EntityKey) {}

  private get sheetName(): KnownSheetName {
    return SHEET_MAPPING[this.entityKey];
  }

  async getAll(
    filters?: FilterOptions,
    pagination?: PaginationOptions,
  ): Promise<ApiResult<DataResponse<T>>> {
    try {
      const result = await readSpreadsheetData<T>(this.sheetName);

      if (!result.success) {
        return { success: false, error: result.error || "Failed to read data" };
      }

      let data = result.data || [];

      // Convert todo data from spreadsheet format if this is a todo entity
      if (this.entityKey === "todoEntries") {
        data = data.map((item) =>
          convertFromSpreadsheetFormat(item as any),
        ) as T[];
      }

      // Apply filters
      if (filters) {
        data = this.applyFilters(data, filters);
      }

      // Apply pagination
      if (pagination) {
        const paginatedResult = this.applyPagination(data, pagination);
        return {
          success: true,
          data: paginatedResult,
        };
      }

      return {
        success: true,
        data: {
          data,
          total: data.length,
          page: 1,
          limit: data.length,
          hasMore: false,
        },
      };
    } catch (error) {
      return {
        success: false,
        error: error instanceof Error ? error.message : "Unknown error",
      };
    }
  }

  async getById(id: string): Promise<ApiResult<T>> {
    try {
      const result = await this.getAll();
      if (!result.success || !result.data) {
        return { success: false, error: result.error || "Failed to get data" };
      }

      const item = result.data.data.find(
        (item: T) => (item as T & { id: string }).id === id,
      );
      if (!item) {
        return { success: false, error: "Item not found" };
      }

      return { success: true, data: item };
    } catch (error) {
      return {
        success: false,
        error: error instanceof Error ? error.message : "Unknown error",
      };
    }
  }

  async create(
    data: Omit<T, "id" | "createdAt" | "updatedAt">,
  ): Promise<ApiResult<T>> {
    try {
      const entityData = addTimestamps(data as T);

      // Convert to spreadsheet format if this is a todo entity
      const spreadsheetData =
        this.entityKey === "todoEntries"
          ? convertToSpreadsheetFormat(entityData as any)
          : entityData;

      const result = await createSpreadsheetRow(
        this.sheetName,
        spreadsheetData,
      );

      if (!result.success) {
        return { success: false, error: result.error };
      }

      return { success: true, data: entityData };
    } catch (error) {
      return {
        success: false,
        error: error instanceof Error ? error.message : "Unknown error",
      };
    }
  }

  async update(id: string, data: Partial<T>): Promise<ApiResult<T>> {
    try {
      // First find the row index
      const allData = await this.getAll();
      if (!allData.success || !allData.data) {
        return { success: false, error: allData.error || "Failed to get data" };
      }

      const itemIndex = allData.data.data.findIndex(
        (item: T) => (item as T & { id: string }).id === id,
      );
      if (itemIndex === -1) {
        return { success: false, error: "Item not found" };
      }

      const updateData = {
        ...data,
        updatedAt: new Date().toISOString(),
      };

      // Convert to spreadsheet format if this is a todo entity
      const spreadsheetData =
        this.entityKey === "todoEntries"
          ? convertToSpreadsheetFormat(updateData as any)
          : updateData;

      const result = await updateSpreadsheetRow(
        this.sheetName,
        itemIndex + 2,
        spreadsheetData,
      ); // +2 for header row and 1-based indexing

      if (!result.success) {
        return {
          success: false,
          error: result.error || "Failed to update data",
        };
      }

      // Return updated data
      const updatedItem = {
        ...allData.data.data[itemIndex],
        ...updateData,
      } as T;
      return { success: true, data: updatedItem };
    } catch (error) {
      return {
        success: false,
        error: error instanceof Error ? error.message : "Unknown error",
      };
    }
  }

  async delete(id: string): Promise<ApiResult<void>> {
    try {
      // First find the row index
      const allData = await this.getAll();
      if (!allData.success || !allData.data) {
        return { success: false, error: allData.error || "Failed to get data" };
      }

      const itemIndex = allData.data.data.findIndex(
        (item: T) => (item as T & { id: string }).id === id,
      );
      if (itemIndex === -1) {
        return { success: false, error: "Item not found" };
      }

      const result = await deleteSpreadsheetRow(this.sheetName, itemIndex + 2); // +2 for header row and 1-based indexing

      if (!result.success) {
        return {
          success: false,
          error: result.error || "Failed to delete data",
        };
      }

      return { success: true, data: undefined };
    } catch (error) {
      return {
        success: false,
        error: error instanceof Error ? error.message : "Unknown error",
      };
    }
  }

  private applyFilters(data: T[], filters: FilterOptions): T[] {
    return data.filter((item) => {
      // Date range filter
      if (filters.dateRange) {
        const itemDate = (item as T & { date?: string }).date;
        if (itemDate) {
          const date = new Date(itemDate);
          if (
            filters.dateRange.start &&
            date < new Date(filters.dateRange.start)
          ) {
            return false;
          }
          if (filters.dateRange.end && date > new Date(filters.dateRange.end)) {
            return false;
          }
        }
      }

      // Status filter
      if (
        filters.status &&
        (item as T & { status?: string }).status !== filters.status
      ) {
        return false;
      }

      // Category filter
      if (
        filters.category &&
        (item as T & { category?: string }).category !== filters.category
      ) {
        return false;
      }

      // Search filter
      if (filters.search) {
        const searchLower = filters.search.toLowerCase();
        const searchableFields = ["title", "description", "name", "notes"];
        const hasMatch = searchableFields.some((field) => {
          const value = (item as Record<string, unknown>)[field];
          return (
            typeof value === "string" &&
            value.toLowerCase().includes(searchLower)
          );
        });
        if (!hasMatch) return false;
      }

      return true;
    });
  }

  private applyPagination(
    data: T[],
    pagination: PaginationOptions,
  ): DataResponse<T> {
    const { page = 1, limit = 10 } = pagination;
    const startIndex = (page - 1) * limit;
    const endIndex = startIndex + limit;
    const paginatedData = data.slice(startIndex, endIndex);

    return {
      data: paginatedData,
      total: data.length,
      page,
      limit,
      hasMore: endIndex < data.length,
    };
  }
}

// Specific service instances
class WorkoutLogService extends BaseDataService<WorkoutLogEntry> {
  constructor() {
    super("workoutLogs");
  }

  async getByMuscleGroup(
    muscleGroup: string,
  ): Promise<ApiResult<WorkoutLogEntry[]>> {
    const result = await this.getAll({ muscleGroup });
    if (!result.success || !result.data) {
      return { success: false, error: result.error };
    }
    return { success: true, data: result.data.data };
  }

  async getByDateRange(
    start: string,
    end: string,
  ): Promise<ApiResult<WorkoutLogEntry[]>> {
    const result = await this.getAll({ dateRange: { start, end } });
    if (!result.success || !result.data) {
      return { success: false, error: result.error };
    }
    return { success: true, data: result.data.data };
  }
}

