class TodoService extends BaseDataService<TodoEntry> {
  constructor() {
    super("todoEntries");
  }

  async getByCategory(category: string): Promise<ApiResult<TodoEntry[]>> {
    const result = await this.getAll();
    if (!result.success || !result.data)
      return { success: false, error: result.error };

    const filtered = result.data.data.filter(
      (todo: TodoEntry) => todo.category === category,
    );
    return { success: true, data: filtered };
  }

  async getByStatus(
    status: "active" | "completed",
  ): Promise<ApiResult<TodoEntry[]>> {
    const result = await this.getAll();
    if (!result.success || !result.data)
      return { success: false, error: result.error };

    const filtered = result.data.data.filter(
      (todo: TodoEntry) => todo.status === status,
    );
    return { success: true, data: filtered };
  }

  async markComplete(id: string): Promise<ApiResult<TodoEntry>> {
    return this.update(id, markTodoComplete());
  }

  async markIncomplete(id: string): Promise<ApiResult<TodoEntry>> {
    return this.update(id, markTodoIncomplete());
  }
}

