// Unified Data Service - Single entry point for all data operations
export class DataService {
  public readonly workoutLogs = new WorkoutLogService();
  public readonly exercises = new ExerciseService();
  public readonly workoutTemplates = new WorkoutTemplateService();
  public readonly workoutSessions = new WorkoutSessionService();
  public readonly vitalSigns = new VitalSignsService();
  public readonly bodyMeasurements = new BodyMeasurementService();
  public readonly nutrition = new NutritionService();
  public readonly meditation = new MeditationService();
  public readonly health = new HealthService();
  public readonly sleep = new SleepService();
  public readonly todos = new TodoService();
  public readonly todoCategories = new TodoCategoryService();

  // Batch operations
  async batchCreate<K extends EntityKey>(
    entityKey: K,
    items: Array<Omit<EntityType<K>, "id" | "createdAt" | "updatedAt">>,
  ): Promise<ApiResult<EntityType<K>[]>> {
    try {
      const service = this.getService(entityKey);
      const results: EntityType<K>[] = [];

      for (const item of items) {
        const result = await service.create(item);
        if (!result.success) {
          return { success: false, error: result.error };
        }
        results.push(result.data as EntityType<K>);
      }

      return { success: true, data: results };
    } catch (error) {
      return {
        success: false,
        error: error instanceof Error ? error.message : "Unknown error",
      };
    }
  }

  private getService(
    entityKey: EntityKey,
  ): BaseDataService<EntityType<typeof entityKey>> {
    switch (entityKey) {
      case "workoutLogs":
        return this.workoutLogs as BaseDataService<
          EntityType<typeof entityKey>
        >;
      case "exercises":
        return this.exercises as BaseDataService<EntityType<typeof entityKey>>;
      case "workoutTemplates":
        return this.workoutTemplates as BaseDataService<
          EntityType<typeof entityKey>
        >;
      case "workoutSessions":
        return this.workoutSessions as BaseDataService<
          EntityType<typeof entityKey>
        >;
      case "vitalSigns":
        return this.vitalSigns as BaseDataService<EntityType<typeof entityKey>>;
      case "bodyMeasurements":
        return this.bodyMeasurements as BaseDataService<
          EntityType<typeof entityKey>
        >;
      case "nutritionEntries":
        return this.nutrition as BaseDataService<EntityType<typeof entityKey>>;
      case "meditationEntries":
        return this.meditation as BaseDataService<EntityType<typeof entityKey>>;
      case "healthEntries":
        return this.health as BaseDataService<EntityType<typeof entityKey>>;
      case "sleepEntries":
        return this.sleep as BaseDataService<EntityType<typeof entityKey>>;
      case "todoEntries":
        return this.todos as BaseDataService<EntityType<typeof entityKey>>;
      case "todoCategories":
        return this.todoCategories as BaseDataService<
          EntityType<typeof entityKey>
        >;
      default:
        throw new Error(`Unknown entity key: ${entityKey}`);
    }
  }
}

// Export singleton instance
export const dataService = new DataService();
